export default {
	data() {
		return {
			tel: '',
			telIcon: false,
			code: '',
			codeIcon: false,
			btnData: '获取验证码',
			disabled: true,
			pass: '',
			passIcon: false,
			password: true
		}
	},
	methods: {
		tapBack:function(e){
			uni.navigateBack() 
		},
		tapTel: function(e) {
			this.tel = e.target.value;
			if (e.target.value.length > 0) {
				this.telIcon = true;
			} else {
				this.telIcon = false;
			}

			if (e.target.value.length == 11) {
				this.disabled = false
			} else {
				this.disabled = true
			}
		},
		clearTelIcon: function() {
			this.tel = '';
			this.telIcon = false;
			this.disabled = true
		},
		tapCode: function(e) {
			this.code = e.target.value;
			if (e.target.value.length > 0) {
				this.codeIcon = true;
			} else {
				this.codeIcon = false;
			}
		},
		clearCodeIcon: function() {
			this.code = '';
			this.codeIcon = false;
		},
		countdown: function(time) {
			let that = this;
			if (time == 0) {
				that.btnData = '获取验证码'
				that.disabled = false
			} else {
				time--
				that.btnData = time + 's后获取'
				that.disabled = true
				setTimeout(function() {
					that.countdown(time)
				}, 1000)
			}
		},
		gainCode: function() {
			if (/^(13[0-9]|14[5|6|7]|15[0|1|2|3|5|6|7|8|9]|166|17[0|1|3|5|6|7]|18[0-9]|19[8|9])\d{8}$/.test(this.tel)) {
				uni.request({
					url: this.$Url+'/api/sms?mobile='+this.tel,
					method: 'GET',
					data: {},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.statusCode == 200) {
							this.countdown(300)
							this.disabled = true
							uni.showToast({
								title: '发送成功',
								duration: 1000
							});
						} else {
							uni.showToast({
								icon: 'none',
								title: '网络不给力，请稍后重试',
								duration: 1000
							});
						}
					}
				});
			} else {
				uni.showToast({
					icon:'none',
					title: '请输入正确的手机号码',
					duration: 1000
				});
			}
		},
		tapPass: function(e) {
			this.pass = e.target.value;
			if (e.target.value.length > 0) {
				this.passIcon = true;
			} else {
				this.passIcon = false;
			}
		},
		clearPassIcon: function() {
			this.pass = '';
			this.passIcon = false;
		},
		tapSee: function(e) {
			this.password = !this.password;
		},
		formSubmit: function(e) {
			if(this.tel.length < 1){
				uni.showToast({
					icon:'none',
					title: '请输入手机号码',
					duration: 1000
				});
			}else if(this.code.length < 1){
				uni.showToast({
					icon:'none',
					title: '请输入输入动态密码',
					duration: 1000
				});
			}else if (this.pass.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码',
					duration: 1000
				});
			}else{
				// 账号注册
				uni.request({
					url:  this.$Url+'/api/check',
					method: 'POST',
					data: {
						mobile:this.tel,
						code:this.code
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if(res.data.status_code == 200){
							uni.request({
								url:  this.$Url + '/api/register',
								method: 'POST',
								data: {
									username:this.tel,
									phone:this.tel,
									password:this.pass
								},
								header: {
									'content-type': 'application/x-www-form-urlencoded'
								},
								success: (res) => {
									if(res.data.code == 200){
										uni.showToast({
											title: '恭喜您注册成功！',
											duration: 1000
										});
										uni.navigateTo({
										    url: '../login/login'
										});
									}else{
										uni.showToast({
											icon: res.data.msg,
											title: '网络不给力，请稍后重试',
											duration: 1000
										});
									}
								}
							});
						
						}else{
							uni.showToast({
								icon: 'none',
								title: res.data.msg,
								duration: 1000
							});
						}
					}
				});
			}
		}
	}
}
